<template>
    <div v-if="book">

        <!-- Heading -->
        <h2 class="mb-3">
            {{ book.title }}
            <small class="d-block d-sm-inline">
                {{ book.author }}
            </small>
        </h2>

        <!-- Info -->
        <p v-if="book.isbn">
            <strong>{{ $t('library.isbn') }}:</strong> {{ book.isbn }}
        </p>
        <p v-if="book.language">
            <strong>{{ $t('app.language') }}:</strong> {{ book.language }}
        </p>

        <b-tabs content-class="mt-3">

            <!-- Lendings -->
            <b-tab :title="$t('library.lendings')" active>

                <template v-if="lending">
                    <b-alert
                        v-if="isOverdue"
                        variant="danger"
                        show
                    >
                        {{ $t('library.book_is_overdue') }}
                    </b-alert>
                    <b-alert
                        v-else-if="isSoonOverdue"
                        variant="warning"
                        show
                    >
                        {{ $t('library.book_is_overdue_soon') }}
                    </b-alert>

                    <!-- Person info -->
                    <b-alert
                        variant="info"
                        show
                        v-html="lentToPersonMessage"
                    >
                    </b-alert>

                    <p>
                        <b-button
                            :to="{ name: 'library.lending.person', params: { personId: lending.person.public_id }}"
                            variant="primary"
                        >
                            <font-awesome-icon icon="user" />
                            {{ $t('library.borrower') }}
                        </b-button>

                        <!-- Return book -->
                        <b-button
                            variant="success"
                            :disabled="busy"
                            @click="returnBook"
                        >
                            <font-awesome-icon icon="inbox" />
                            {{ $t('library.return') }}
                        </b-button>

                        <!-- Extend lending -->
                        <b-button
                            variant="primary"
                            :disabled="busy"
                            @click="extendLending"
                        >
                            <font-awesome-icon icon="calendar-plus" />
                            {{ $t('library.extend') }}
                        </b-button>
                    </p>
                </template>
                <template v-else>
                    <b-alert variant="success">
                        {{ $t('library.book_is_available') }}
                    </b-alert>

                    <!-- Lend modal button -->
                    <p>
                        <b-button
                            variant="primary"
                            :disabled="busy"
                            v-b-modal.lendBookModal
                        >
                            <font-awesome-icon icon="plus-circle" />
                            {{ $t('library.lend_book') }}
                        </b-button>
                    </p>

                    <!-- Lend book modal -->
                    <b-modal
                        id="lendBookModal"
                        :title="$t('library.lend_book')"
                        @ok="lendBook"
                        @hidden="selectedPersonId = null"
                    >
                        <person-autocomplete-input
                            @select="selectedPersonId = $event"
                        />
                        <template v-slot:modal-footer="{ ok }">

                            <!-- Lend book button -->
                            <b-button
                                variant="primary"
                                :disabled="!selectedPersonId || busy"
                                @click="ok()"
                            >
                                <font-awesome-icon icon="check" />
                                {{ $t('library.lend_book') }}
                            </b-button>

                        </template>
                    </b-modal>

                </template>

            </b-tab>

            <!-- Logs -->
            <b-tab :title="$t('app.log')" lazy>
                <book-log-table
                    :book-id="bookId"
                />
            </b-tab>

        </b-tabs>

    </div>
    <p v-else>
        {{ $t('app.loading') }}
    </p>
</template>

<script>
import moment from 'moment'
import { showSnackbar } from '@/utils'
import libraryApi from '@/api/library'
import PersonAutocompleteInput from '@/components/people/PersonAutocompleteInput'
import BookLogTable from '@/components/library/BookLogTable'
export default {
    components: {
        PersonAutocompleteInput,
        BookLogTable
    },
    props: {
        bookId: {
            required: true
        }
    },
    data () {
        return {
            book: null,
            lending: null,
            defaultExtendDuration: 0,
            busy: false,
            selectedPersonId: null
        }
    },
    computed: {
        isOverdue () {
            return moment(this.lending.return_date).isBefore(moment(), 'day')
        },
        isSoonOverdue () {
            return moment(this.lending.return_date).isSame(moment(), 'day')
        },
        lentToPersonMessage () {
            if (this.lending.person) {
                return this.$t('library.book_is_lent_to_person_until', {
                    person: this.lending.person.full_name,
                    until: moment(this.lending.return_date).format("LL")
                })
            }
            // TODO
            // @php
            //     $thrashedPerson = $lending->person()->withTrashed()->first();
            // @endphp
            // @isset($thrashedPerson)
            //     @lang('library.book_is_lent_to_soft_deleted_person_until', [ 'person' => $thrashedPerson->fullName, 'until' => $lending->return_date->toDateString() ])
            // @else -->
            return this.$t('library.book_is_lent_to_deleted_person_until', {
                until: moment(this.lending.return_date).format("LL")
            })
        }
    },
    created () {
        this.loadBook()
        this.loadLending()
    },
    methods: {
        moment,
        async loadBook () {
            try {
                let data = await libraryApi.findBook(this.bookId)
                this.book = data.data
            } catch (err) {
                alert(err)
            }
        },
        async loadLending () {
            this.busy = true
            try {
                let data = await libraryApi.findLendingOfBook(this.bookId)
                if (data.data) {
                    this.lending = data.data
                    this.defaultExtendDuration = data.meta.default_extend_duration
                } else {
                    this.lending = null
                }
            } catch (err) {
                alert(err)
            }
            this.busy = false
        },
        async extendLending () {
            var days = prompt(`${this.$t('app.number_of_days')}:`, this.defaultExtendDuration)
            if (days != null && days > 0) {
                this.busy = true
                try {
                    let data = await libraryApi.extendLending(this.bookId, days)
                    showSnackbar(data.message)
                    this.loadLending()
                } catch (err) {
                    alert(err)
                }
                this.busy = false
            }
        },
        async returnBook () {
            this.busy = true
            try {
                let data = await libraryApi.returnBook(this.bookId)
                showSnackbar(data.message)
                this.loadLending()
            } catch (err) {
                alert(err)
            }
            this.busy = false
        },
        async lendBook (bvModalEvt) {
            bvModalEvt.preventDefault()
            if (this.selectedPersonId) {
                this.busy = true
                try {
                    let data = await libraryApi.lendBook(this.bookId, this.selectedPersonId)
                    showSnackbar(data.message)
                    this.loadLending()
                    this.$nextTick(() => {
                        this.$bvModal.hide('lendBookModal')
                    })
                } catch (err) {
                    alert(err)
                }
                this.busy = false
            }
        }
    }
}
</script>
